.demo-wrapper {
  min-width: 750px;
  margin-bottom: 40px;
}

.demo-editor {
  background-color: #ffff;
  border-radius: 10px;
  box-shadow: 1px 1px 10px 0px rgba(245, 245, 245, 0.208);
  padding: 10px;
  min-width: 750px;
  min-height: 300px;
  color: #000;  
  font-family: "Montserrat";
  font-size: 16px;
}

.demo-toolbar {
  border: 1px solid #ccc;
  margin-bottom: 10px;
}

.rdw-list-dropdown {
  width: 50px;
  z-index: 0;
}

.rdw-text-align-dropdown {
  width: 50px;
  z-index: 0;
}